



















































import Measurement from '@/components/Measurement.vue';
import Vue from 'vue';

// import TravelLog from '../models/TravelLog';
import Universe from '../models/Universe'
import Generator from '../models/Generator'

export default Vue.extend({
  components: { Measurement },
  props: {
  },
  data: function (){
    return {
      recentlyScanned: [],
      newlyDiscovered: [
        new Universe(Generator.generateUnseededName()),
        new Universe(Generator.generateUnseededName()),
        // new Universe(Generator.generateUnseededName()),
        // new Universe(Generator.generateUnseededName()),
        // new Universe(Generator.generateUnseededName())
      ],
      discoveryProgress: Math.random() * 10 + 5
      // travelLogs: new TravelLog().getLogs()
    }
  },
  methods: {
    discoverNewUniverseLoop() {
      if (this.discoveryProgress >= 100) {
          this.newlyDiscovered.unshift(new Universe(Generator.generateUnseededName()))
          this.discoveryProgress = 0
      } else {
          this.discoveryProgress = Math.min(100, this.discoveryProgress + Math.random() * 0.5)
      }

      setTimeout(this.discoverNewUniverseLoop, Math.random() * 40 + 10)
    }
  },
  created() {
    this.discoverNewUniverseLoop()
  }
})
